html, body, #root {
  height: 100%;
  width:100%;
}

.App-title {
  cursor: pointer;
  color: white;
  font-family: 'Rubik-mono', serif;
  font-weight: 200;
}

.About-screen:hover {
}

@media (min-width: 800px) {
  .App-header {
    width:100%;
    height:5%;
    padding-bottom:5%;
    background:-webkit-linear-gradient(340deg,rgb(19, 19, 19), transparent 20%);
    background:-o-linear-gradient(340deg,rgb(19, 19, 19), transparent 20%);
    background:linear-gradient(110deg,rgb(19, 19, 19), transparent 20%)
  }

  .App {
    height: 100%;
    width:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .App-bg {
    position:absolute;
    background-image: url("bluewirebackground.png");
    -webkit-filter: blur(5px);
            filter: blur(5px);
    width:100%;
    height:100%;
    z-index:-1;
  }

  .Screen-window {
    height: 90%;
    width: 100%;
    overflow-y: scroll;
    color: white;
  }

  .Screen-contents {
    width:80%;
    padding-left:10%;
  }

  .App-title {
    font-size: 30px;
    width:20%;
    height:10%;
    position:absolute;
    float:left;
  }

  .App-title:hover {
    color: #EBDEF6;
  }

  .App-headerbuttons {
    position:absolute;
    margin-left: 20%;
    padding-left: 5%;
    margin-top:3%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width:60%;
  }
  
  .Button-link {
    cursor: pointer;
    color: rgb(255, 255, 255);
    width: 100%;
    font-family: 'Heebo-bold', sans-serif;
    font-size:12px;
    font-weight: 200;
    padding-left:2%;
  }
  .Button-link:hover {
    color: #EBDEF6;
  }
}
@media (max-width: 799px) {
  .App-header {
    margin-right: 5%;
    width:20%;
    height:100%;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .App {
    height: 100%;
    width:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .Screen-window {
    height: 100%;
    width: 80%;
    background:-webkit-gradient(linear,left top, left bottom,from(#EBDEF6), to(white));
    background:-webkit-linear-gradient(top,#EBDEF6, white);
    background:-o-linear-gradient(top,#EBDEF6, white);
    background:linear-gradient(180deg,#EBDEF6, white);
    overflow-y: scroll;
  }

  .App-title {
    font-size: 20px;
  }

  .App-headerbuttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  
  .Button-link {
    cursor: pointer;
    color: rgb(54, 54, 54);
    width: 100%;
    margin: 10% 0 10% 2%;
    font-family: 'Heebo-bold', sans-serif;
    font-size:16px;
    font-weight: bolder;
    text-shadow: 0px 2px #8b8b8b, 0px 1px 2px rgb(0, 0, 0);
  }
}

@font-face {
  font-family: 'Heebo-bold';
  src: local('Heebo-bold'), url(./fonts/Heebo-Bold.ttf) format('.ttf');
}

@font-face {
  font-family: "Rubik-mono";
  src: url("./fonts/RubikMonoOne-Regular.ttf");
}


